<template>
    <v-container fluid fill-height style="padding:0;" id="font" grid-list-md>
        <Blog :items="items" :time="time" :title="title">
            <div class="blogcontent">
                <v-layout row wrap>
                    <v-flex xs12>
                        <v-hover>
                            <v-card
                                slot-scope="{ hover }"
                                :class="`elevation-${hover ? 12 : 2}`"
                                min-height="200"
                            >
                                <v-card-title>
                                    <h3 id="Explain">解释</h3>
                                </v-card-title>
                                <v-divider></v-divider>
                                <v-card-text>
                                    <p class=".body-1">1. <code>clip-path: inset(20% 12% 15% 25% round 5px);</code>定义一个矩形，参数表示四个点距离边框的距离，round后是可选的圆角参数</p>
                                    <p class=".body-1">2. <code>clip-path: circle(35% at 50% 50%);</code>定义一个圆形，第一个参数是圆的半径，at后面的参数是圆心的位置</p>
                                    <p class=".body-1">3. <code>clip-path: ellipse(30% 22% at 50% 50%);</code>定义一个椭圆，at前的两个参数是椭圆长短轴的长度，后面的两个参数是位置</p>
                                    <p class=".body-1">4. <code>clip-path: polygon(10% 20%, 70% 80%, 10% 80%);</code>定义任意的多边形，逗号分隔的每一对参数表示一个点的坐标</p>
                                </v-card-text>
                            </v-card>
                        </v-hover>
                    </v-flex>
                </v-layout>
                <v-layout row wrap>
                    <v-flex sm12>
                        <v-hover>
                            <v-card
                                slot-scope="{ hover }"
                                :class="`elevation-${hover ? 12 : 2}`"
                                min-height="300"
                            >
                                <v-card-title>
                                    <h3 id="Result1">效果</h3>
                                </v-card-title>
                                <v-divider></v-divider>
                                <v-card-text>
                                    <div class="geometry rec"></div>
                                    <div class="geometry circle"></div>
                                    <div class="geometry ellipse"></div>
                                    <div class="geometry triangle"></div>
                                    <div class="geometry parallelogram"></div>
                                    <div class="geometry pentagon"></div>
                                    <div class="geometry hexagon"></div>
                                    <div class="geometry animate-hexagon"></div>
                                </v-card-text>
                            </v-card>
                        </v-hover>
                    </v-flex>
                    <v-flex sm12>
                        <v-hover>
                            <v-card
                                slot-scope="{ hover }"
                                :class="`elevation-${hover ? 12 : 2}`"
                                min-height="300"
                            >
                                <v-card-title>
                                    <h3 id="Code1">代码</h3>
                                </v-card-title>
                                <v-divider></v-divider>
                                <v-tabs color="light-green darken-1" dark slider-color="yellow">
                                    <v-tab key="html" ripple>HTML</v-tab>
                                    <v-tab key="css" ripple>CSS</v-tab>
                                    <v-tab-item key="html">
                                        <v-card flat>
                                            <v-card-text>
                                                <pre class="code">
                                                    {{html}}
                                                </pre>
                                            </v-card-text>
                                        </v-card>
                                    </v-tab-item>
                                    <v-tab-item key="css">
                                        <v-card flat>
                                            <v-card-text>
                                                <pre class="code">{{css}}</pre>
                                            </v-card-text>
                                        </v-card>
                                    </v-tab-item>
                                </v-tabs>
                            </v-card>
                        </v-hover>
                    </v-flex>
                </v-layout>
            </div>
        </Blog>
    </v-container>
</template>

<script>
import Blog from "../Blog.vue";
export default {
    components: { Blog },
    data() {
        return {
            time: "2018-9-16",
            title: "clip-path创建任意多边几何体",
            items: [
                {
                    title: "解释",
                    target: "#Explain"
                },
                { divider: true },
                {
                    title: "效果",
                    target: "#Result1"
                },
                { divider: true },
                {
                    title: "代码",
                    target: "#Code1"
                }
            ],
            html: `
            <div class="geometry rec"></div>
            <div class="geometry circle"></div>
            <div class="geometry ellipse"></div>
            <div class="geometry triangle"></div>
            <div class="geometry parallelogram"></div>
            <div class="geometry pentagon"></div>
            <div class="geometry hexagon"></div>
            <div class="geometry animate-hexagon"></div>
            `,
            css: `
            .geometry{
                width: 100px;
                height: 100px;
                float: left;
                background-color: blueviolet;
            }

            .rec{
                clip-path: inset(20% 12% 15% 25% round 5px);
            }
            .circle{
                clip-path: circle(35% at 50% 50%);
                shape-outside: circle(50%);
            }
            .ellipse{
                clip-path: ellipse(30% 22% at 50% 50%);
            }
            .triangle{
                clip-path: polygon(10% 20%, 70% 80%, 10% 80%);
            }
            .parallelogram{
                clip-path: polygon(20% 20%, 80% 20%, 70% 80%, 10% 80%);
            }
            .pentagon{
                clip-path: polygon(50% 20%, 80% 50%, 70% 80%, 30% 80%, 20% 50%);
            }
            .hexagon{
                clip-path: polygon(30% 20%, 70% 20%, 90% 50%, 70% 80%, 30% 80%, 10% 50%);
            }
            
            .animate-hexagon{
                width: 200px;
                height: 200px;
                clip-path: polygon(30% 20%, 70% 20%, 90% 50%, 70% 80%, 30% 80%, 10% 50%);
                animation: 3s hexagon infinite alternate linear;
            }

            @keyframes hexagon {
                from {
                    clip-path: polygon(30% 20%, 70% 20%, 90% 50%, 70% 80%, 30% 80%, 10% 50%);
                }

                to {
                    clip-path: polygon(30% 50%, 70% 50%, 50% 50%, 70% 50%, 30% 50%, 50% 50%);
                }
            }
            `
        };
    },

    mounted() {}
};
</script>

<style lang="scss">
#font {
    .blogcontent {
        padding-top: 20px;
    }
    .code {
        font-size: 12px;
        background-color: #f9f9f5;
        white-space: pre-wrap;
        word-wrap: break-word;
    }

    .geometry{
        width: 100px;
        height: 100px;
        float: left;
        background-color: blueviolet;
    }

    .rec{
        clip-path: inset(20% 12% 15% 25% round 5px);
    }
    .circle{
        clip-path: circle(35% at 50% 50%);
        shape-outside: circle(50%);
    }
    .ellipse{
        clip-path: ellipse(30% 22% at 50% 50%);
    }
    .triangle{
        clip-path: polygon(10% 20%, 70% 80%, 10% 80%);
    }
    .parallelogram{
        clip-path: polygon(20% 20%, 80% 20%, 70% 80%, 10% 80%);
    }
    .pentagon{
        clip-path: polygon(50% 20%, 80% 50%, 70% 80%, 30% 80%, 20% 50%);
    }
    .hexagon{
        clip-path: polygon(30% 20%, 70% 20%, 90% 50%, 70% 80%, 30% 80%, 10% 50%);
    }
    
    .animate-hexagon{
        width: 200px;
        height: 200px;
        clip-path: polygon(30% 20%, 70% 20%, 90% 50%, 70% 80%, 30% 80%, 10% 50%);
        animation: 3s hexagon infinite alternate linear;
    }

    @keyframes hexagon {
        from {
            clip-path: polygon(30% 20%, 70% 20%, 90% 50%, 70% 80%, 30% 80%, 10% 50%);
        }

        to {
            clip-path: polygon(30% 50%, 70% 50%, 50% 50%, 70% 50%, 30% 50%, 50% 50%);
        }
    }
}
</style>

